.overlay {
  opacity: 0;
  visibility: hidden;
  position: fixed;

  z-index: 20;
  top: 100px;
  width: 100%;
  height: 100%;
  background-color: var(--main-color);
  transition: opacity 500ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 500ms cubic-bezier(0.4, 0, 0.2, 1);
}
.modal {
  // position: relative;

  opacity: 0;
  visibility: hidden;
  z-index: 30;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 280px;
  max-height: 990px;
  padding: 28px;

  // overflow: scroll;

  background-color: var(--modal-bg-color);
  border: 2px solid #f8d24e;
  border-radius: 10px;
  transform: translate(-50%, -50%);
  transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1),
    min-height 500ms cubic-bezier(0.4, 0, 0.2, 1);
  @include tablet {
    width: 528px;
    padding: 32px 60px;
  }
  // .backdrop.is-hidden .modal {
  //   transform: translate(-50%, -50%) scale(0);
  //   min-height: 0;
  // }
  .modal-close {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 8px;
    right: 8px;
    width: 26px;
    height: 26px;
    border: none;

    color: #131313;
    background: var(--modal-bg-color);
    transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1),
      color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover,
    &:focus {
      color: #f8d24e;
    }
    @include tablet {
      top: 20px;
      right: 20px;
      width: 32px;
      height: 32px;
    }
  }
  .close-icon {
    fill: currentColor;
  }

  // .is-hidden {
  //   opacity: 0;
  //   visibility: hidden;
  //   pointer-events: none;
  // }
}
.modal.active,
.overlay.active {
  opacity: 1;
  visibility: visible;
}
